/**
 * dashboard-setup.scss
 * Styles for WooCommerce dashboard finish setup widgets
 * only loaded on the dashboard itself.
 */
/**
 * Styling begins
 */
.dashboard-widget-finish-setup .progress-wrapper {
  border: 1px solid #757575;
  border-radius: 16px;
  font-size: 0.9em;
  padding: 2px 8px 2px 8px;
  display: inline-block;
  box-sizing: border-box;
}
.dashboard-widget-finish-setup .progress-wrapper > span {
  position: relative;
  top: -3px;
  color: #757575;
}
.dashboard-widget-finish-setup .description div {
  margin-top: 11px;
  float: left;
  width: 70%;
}
.dashboard-widget-finish-setup .description img {
  float: right;
  width: 30%;
}
.dashboard-widget-finish-setup .circle-progress {
  margin-top: 1px;
  margin-left: -3px;
}
.dashboard-widget-finish-setup .circle-progress circle {
  stroke: #f0f0f0;
  stroke-width: 1px;
}
.dashboard-widget-finish-setup .circle-progress .bar {
  stroke: #949494;
}